.split{
  position: relative;
  margin: 0 auto;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  &:before,
  &:after{
    content: '';
    position: absolute;
    width: 1px;
    background-color: $dark;
    display: block;
    top: 50%;
  }
  &:before{
    left: 0;
    transform: translate(20px,-50%) rotate(15deg);
  }
  &:after{
    right: 0;
    transform: translate(-20px,-50%) rotate(15deg);
  }
}

.m-card{
  border-radius: 20px;
  background-color: $black;
}

.match-card{
  display: grid;
  grid-template-columns: 430px auto 430px;
  gap: 20px;
  padding: 30px 60px;
  border: 3px solid $black;
  transition: all .3s;
  overflow: hidden;
  &:not(:last-child){
    margin-bottom: 30px;
  }
  &:hover{
    border-color: #4568DC;
  }
}

.played-team{
  display: grid;
  grid-template-columns: 63.5% 27%;
  justify-content: space-between;
  &--reverse{
    grid-template-columns: 27% 63.5%;
    .played-team__content{
      order: 2;
    }
  }
  &__content{
    padding-top: 18px;
  }
  &__logo{
    padding-bottom: 100%;
    position: relative;
    display: block;
    img{
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      object-fit: cover;
      display: block;
    }
  }
  &__players{
    display: flex;
    align-items: center;
    padding: 0;
    margin: 29px 0 0 0;
  }
  &__player{
    list-style: none;
    padding: 0;
    margin: 0;
    width: 56px;
    height: 56px;
    flex-shrink: 0;
    position: relative;
    overflow: hidden;
    border-radius: 50%;
    &:before{
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      border: 3px solid $black;
      border-radius: 50%;
    }
    img{
      position: absolute;
      top: -3px;
      left: -3px;
      width: calc(100% + 6px);
      height: calc(100% + 6px);
      object-fit: cover;
      border-radius: 50%;
    }
    &:not(:first-child){
      margin-left: -13px;
    }
  }
}

.match-result{
  text-align: center;
  max-width: 700px;
  display: block;
  &:before,
  &:after{
    height: 160px;
  }
  &__top{
    padding-top: 10px;
    position: relative;
  }
  &__add-time{
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
  }
  &__date{
    margin-top: 7px;
  }
}

.broadcast-card{
  position: relative;
  display: block;
  border-radius: 20px;
  overflow: hidden;
  &:before{
    content: '';
    position: absolute;
    border: 15px solid $dark;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
    border-radius: inherit;
  }
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 50%;
    min-height: 85px;
    background: linear-gradient(180deg, #171717 0%, rgba(23, 23, 23, 0) 100%), linear-gradient(180deg, #171717 0%, rgba(23, 23, 23, 0) 100%);
    z-index: 2;
  }
  &__img{
    position: relative;
    padding-bottom: 44.13%;
    img{
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      object-fit: cover;
      display: block;
    }
  }
  &__content{
    position: absolute;
    z-index: 4;
    padding: 30px;
    width: 100%;
    top: 0;
    left: 0;
  }
  &__date{
    margin-top: 10px;
  }
}

.player-card{
  margin-top: 30px;
  padding: 40px;
  display: grid;
  grid-template-columns: 400px auto 400px;
  gap: 20px;
  &__type{
    max-width: 700px;
    img{
      max-width: 100%;
      width: 145px;
    }
    &:before,
    &:after{
      height: 120px;
    }
  }
}

.player-info{
  display: flex;
  @media screen and (min-width: 1500px){
    &--reverse{
      flex-direction: row-reverse;
    }
  }
  &__images{
    flex-shrink: 0;
    height: 99px;
    width: 99px;
    position: relative;
  }
  &__img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
  &__flag{
    position: absolute;
    bottom: 0;
    left: 2.64px;
  }
  &__content{
    display: grid;
    grid-template-columns: 100%;
    gap: 5px;
    margin: 0 40px;
    flex-grow: 1;
  }
  &__assist{
    width: 41px;
    flex-shrink: 0;
    img{
      width: 100%;
    }
  }
  &__team{
    text-transform: uppercase;
  }
}

.stat-card{
  padding: 18px 20px;
  display: grid;
  grid-template-columns: 27.2% auto 27.2%;
  gap: 15px;
  &__num{
    height: 79px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 20px;
    background: #1E1E20;
  }
  &__title{

    max-width: 350px;
    &:before,
    &:after{
      height: 86px;
    }
  }
}

.summary-card{
  margin: 10px 0;
  min-height: 97px;
  display: grid;
  align-items: center;
  grid-template-columns: calc(50% - 102.5px) 205px calc(50% - 102.5px);
  padding: 15px 30px;
  &__center{
    text-align: center;
  }
}

.latest-card{
  display: grid;
  align-items: center;
  grid-template-columns: 30.5% auto 30.5%;
  gap: 9px;
  padding: 20px;
  text-align: center;
  &__img{
    width: 56px;
    height: 56px;
    margin: 0 auto 13px;
    img{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      object-fit: cover;
    }
  }
  &__score{
    min-height: 146px;
    display: flex;
    align-items: center;
    justify-content: center;
    &:before,
    &:after{
      height: 101px;
    }
  }
}

.mvp-card{
  padding: 95px 30px 30px 40px;
  position: relative;
  .player-info__assist{
    width: 53px;
  }
  &__top{
    position: absolute;
    width: 100%;
    height: 118px;
    top: 0;
    left: 0;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__star{
    max-height: 100%;
    display: block;
    &--second{
      max-height: 81.5%;
      margin: 0 25px;
    }
    &--third{
      max-height: 45%;
      margin: 0 14px;
      align-self: flex-end;
    }
  }
}

.stat-row{
  &__ordinal{
    background: linear-gradient(135deg, #4568DC 0%, #AF6AB4 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
  }
  .player-info{
    display: flex;
    align-items: center;
    &__images{
      width: 75px;
      height: 75px;
      margin-bottom: 0;
    }
    &__flag{
      width: 16px;
    }
    &__content{
      margin: 0 0 0 40px;
    }
    &__assist{
      width: 30px;
    }
  }
  &--gk{
    .player-info__assist{
      width: 72px;
    }
  }
}

.stat-row2{
  td{
    &:nth-child(4)~td{
      text-align: center;
    }
  }
}

.play-card{
  padding: 15px 50px 15px 20px;
  position: relative;
  z-index: 2;
  display: grid;
  grid-template-columns: 100%;
  gap: 15px;
  height: 110px;
  border-radius: 20px !important;
  &__item{
    display: flex;
    align-items: center;
    max-width: 350px;
  }
  &__img{
    width: 26px;
    height: 26px;
    flex-shrink: 0;
    margin-right: 10px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__team{
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  &__score{
    padding-left: 10px;
    margin-left: auto;
  }
  &__link{
    position: absolute;
    width: 36px;
    height: 100%;
    top: 0;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: $dark;
    border-radius: 0 20px 20px 0;
  }
  &__info{
    position: absolute;
    bottom: 100%;
    left: 0;
    width: calc(100% - 36px);
    margin-bottom: 20px;
  }
  &__logo{
    max-width: 100%;
    display: block;
    margin-bottom: 50px;
  }
  &__stage{
    display: flex;
    flex-direction: column;
    align-items: center;
    text-transform: uppercase;
  }
  &.final2{
    .play-card__info{
      bottom: unset;
      top: 100%;
      margin-top: 20px;
    }
  }
}




@media screen and (max-width: 1750px){
  .match-card{
    padding: 20px 30px;
  }
}


@media screen and (max-width: 1700px){
  .match-card{
    grid-template-columns: 380px auto 380px;
    gap: 15px;
  }
}


@media screen and (max-width: 1500px){
  .match-card{
    grid-template-columns: repeat(2,calc(50% - 15px));
    gap: 20px 30px;
  }

  .played-team{
    grid-template-columns: 80px calc(100% - 95px) !important;
    gap: 15px;
    &__content{
      order: 2;
    }
    &__player{
      &:nth-child(5)~.played-team__player{
        display: none;
      }
    }
  }

  .match-result{
    max-width: unset;
    order: 2;
    grid-column: 1/-1;
    padding-top: 20px;
    border-top: 1px solid $dark;
    &:before,
    &:after{
      display: none;
    }
  }

  .player-card{
    padding: 25px;
    grid-template-columns: repeat(2,calc(50% - 15px));
    gap: 20px 30px;
    &__type{
      max-width: unset;
      order: 2;
      grid-column: 1/-1;
      padding-top: 20px;
      border-top: 1px solid $dark;
      &:before,
      &:after{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 1200px){
  .played-team{
    grid-template-columns: 50px calc(100% - 65px) !important;
    &__content{
      padding-top: 0;
    }
    &__players{
      margin-top: 20px;
    }
    &__player{
      &:nth-child(5){
        display: none;
      }
    }
  }

  .player-info{
    &__content{
      margin: 0 15px;
    }
  }

  .summary-card{
    grid-template-columns: calc(50% - 50px) 100px calc(50% - 50px);
    min-height: 72px;
  }
}


@media screen and (max-width: 992px){
  .match-card{
    &:not(:last-child){
      margin-bottom: 20px;
    }
  }

  .played-team{
    &__player{
      width: 48px;
      height: 48px;
      &:nth-child(4){
        display: none;
      }
    }
  }

  .match-result{
    &__top{
      padding: 0;
    }
    &__add-time{
      transform: unset;
      position: static;
    }
  }

  .player-info{
    &__images{
      width: 70px;
      height: 70px;
    }
    &__content{
      margin: 0 10px;
    }
    &__assist{
      width: 30px;
    }
  }

  .stat-card{
    gap: 10px;
    grid-template-columns: 20% auto 20%;
  }
}


@media screen and (max-width: 576px){
  .m-card,
  .broadcast-card{
    border-radius: 10px;
  }

  .match-card{
    padding: 15px 20px;
  }

  .played-team{
    display: flex;
    flex-direction: column;
    gap: unset;
    &__logo{
      &-wrap{
        width: 50px;
        order: -1;
        margin-bottom: 10px;
      }
    }
    &__player{
      width: 33px;
      height: 33px;
      &:nth-child(4),
      &:nth-child(5),
      &:nth-child(6){
        display: block !important;
      }
    }
  }

  .broadcast-card{
    &::before{
      border-width: 7px;
    }
    &__content{
      padding: 15px;
    }
  }

  .player-card{
    &__type{
      img{
        width: 90px;
      }
    }
  }

  .player-info{
    position: relative;
    display: block;
    &__images{
      margin: 0 0 20px;
    }
    &__assist{
      position: absolute;
      top: 0;
      right: 0;
    }
  }

  .latest-card{
    border-radius: 10px;
    padding: 10px;
    grid-template-columns: calc(50% - 74px) 130px calc(50% - 74px);
  }

  .mvp-card{
    padding: 50px 15px 15px 20px;
    &__top{
      height: 80px;
    }
    .player-info{
      display: flex;
      flex-direction: column;
      align-items: center;
      text-align: center;
      &__content{
        order: 2;
      }
      &__assist{
        position: static;
      }
    }
  }

  .stat-card{
    &__num{
      height: 50px;
      font-size: 18px;
      border-radius: 10px;
    }
    &__title{
      font-size: 14px;
      &:before,
      &:after{
        height: 50px;
      }
      &:before{
        transform: translate(6px,-50%) rotate(15deg);
      }
      &:after{
        transform: translate(-6px,-50%) rotate(15deg);
      }
    }
  }

  .summary-card{
    padding-left: 20px;
    padding-right: 20px;
    .display20{
      font-size: 14px;
    }
    .display18{
      font-size: 13px;
    }
  }
}
