.m-slider{
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  .swiper{
    &-container{
      margin-top: 30px;
      overflow: hidden;
    }
    &-button{
      position: static;
      margin: 0 0 0 20px;
    }
  }
}

.match-banner{
  padding: 130px 50px;
  background: #171717;
  border-radius: 20px;
  position: relative;
  &__img{
    border-radius: inherit;
    position: absolute;
    bottom: 0;
    right: 0;
    width: 100%;
    object-fit: cover;
    height: 119%;
    mix-blend-mode: screen;
    pointer-events: none;
  }
}

.match-filter{
  margin-top: 60px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  &__title{
    margin-right: auto;
  }
  &__select{
    margin-left: 40px;
    border-color: #FFFFFF;
  }
}

.match-top{
  margin-top: 30px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: center;
  &__item{
    width: 26%;
  }
}

.match-items{
  margin-top: 39px;
}

.broadcast{
  margin-top: 60px;
}

.podcast{
  margin-top: 55px;
  &__img{
    width: calc(100% + 1.5rem);
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__title{
    text-transform: uppercase;
    margin-right: 40px;
    color: #AF6AB4;
  }
  &__items{
    display: flex;
    align-items: center;
  }
  &__item{
    margin-left: 40px;
    display: block;
    img{
      max-width: 100%;
      max-height: 61px;
    }
  }
  &__subtitle{
    text-transform: uppercase;
    margin-top: 6px;
    text-align: right;
    padding-right: 281px;
  }
}

.follow{
  margin-top: 55px;
  &__title{
    text-align: center;
    text-transform: uppercase;
    color: #AF6AB4;
  }
  &__items{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
  }
  &__item{
    margin: 0 15px;
    width: 66px;
    display: block;
    img{
      width: 100%;
    }
  }
}


@media screen and (max-width: 1600px){
  .match-banner{
    padding-top: 80px;
    padding-bottom: 80px;
  }
}

@media screen and (max-width: 1500px){
  .match-top{
    grid-template-columns: repeat(2,calc(50% - 15px));
    gap: 30px;
    &__item{
      width: 100%;
    }
  }

  .match-filter{
    &__select{
      margin-left: 20px;
    }
  }
}


@media screen and (max-width: 1300px){
  .match-banner{
    padding: 50px 30px;
  }
}


@media screen and (max-width: 1200px){
  .match-filter{
    flex-wrap: wrap;
    justify-content: space-between;
    &__title{
      width: 100%;
    }
    &__select{
      margin: 20px 0 0 0;
    }
  }

  .podcast{
    &__top{
      display: block;
    }
    &__items{
      margin-top: 10px;
      flex-wrap: wrap;
    }
    &__item{
      margin: 10px 20px 0 0;
    }
    &__subtitle{
      text-align: left;
      padding: 0;
      margin-top: 15px;
    }
  }

  .follow{
    margin-top: 30px;
    &__items{
      margin-top: 15px;
    }
    &__item{
      width: 50px;
      margin: 0 5px;
    }
  }
}


@media screen and (max-width: 992px){
  .match-banner{
    border-radius: 10px;
    padding: 40px 20px;
  }

  .match-filter{
    &__select{
      width: 100% !important;
    }
  }

  .match-items{
    margin-top: 30px;
  }

  .podcast{
    margin-top: 30px;
  }
}


@media screen and (max-width: 576px){
  .podcast{
    &__item{
      img{
        max-height: 45px;
      }
    }
  }
}
