.display90,
.display50,
.display40,
.display35,
.display30,
.display25,
.display20,
.display18,
.display16,
.display14,
.display12,
.display8{
  line-height: 130%;
}

.display90{
  font-size: 90px;
}

.display50{
  font-size: 50px;
}

.display40{
  font-size: 40px;
}

.display35{
  font-size: 35px;
}

.display30{
  font-size: 30px;
}

.display25{
  font-size: 25px;
}

.display20{
  font-size: 20px;
}

.display18{
  font-size: 18px;
}

.display16{
  font-size: 16px;
}

.display12{
  font-size: 12px;
}

.display14{
  font-size: 14px;
}

.display8{
  font-size: 8px;
}

.bold-text{
  font-weight: 700;
}

.medium-text{
  font-weight: 500;
}

.light-text{
  font-weight: 300;
}

.gray-text{
  color: $gray;
}

.yellow-text{
  color: $yellow;
}

.red-text{
  color: $red;
}

.green-text{
  color: $green;
}


@media screen and (max-width: 1600px){
  .display90{
    font-size: 75px;
  }
}


@media screen and (max-width: 1200px){
  .display90{
    font-size: 50px;
  }

  .display50{
    font-size: 35px;
  }

  .display40{
    font-size: 30px;
  }

  .display35{
    font-size: 28px;
  }

  .display30{
    font-size: 24px;
  }

  .display25{
    font-size: 20px;
  }

  .display20{
    font-size: 18px;
  }
}


@media screen and (max-width: 992px){
  .display90{
    font-size: 40px;
  }

  .display50{
    font-size: 32px;
  }

  .display40{
    font-size: 27px;
  }

  .display35{
    font-size: 24px;
  }

  .display30{
    font-size: 21px;
  }

  .display25{
    font-size: 18px;
  }

  .display20{
    font-size: 16px;
  }

  .display18{
    font-size: 15px;
  }

  .display16{
    font-size: 14px;
  }

  .display14{
    font-size: 13px;
  }
}


@media screen and (max-width: 576px){
  .display90{
    font-size: 32px;
  }

  .display50{
    font-size: 27px;
  }

  .display40{
    font-size: 24px;
  }

  .display35{
    font-size: 22px;
  }

  .display30{
    font-size: 20px;
  }
}
