.result{
  padding: 75px 100px;
  background-color: $black;
  border-radius: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__logo{
    flex-shrink: 0;
    display: block;
    width: 237px;
    height: 237px;
    padding: 21px;
    border: 2px solid $blue;
    border-radius: 50%;
    position: relative;
    overflow: hidden;
    &:before{
      content: '';
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      background: rgba(69, 104, 220, 0.4);
      filter: blur(13.5px);
    }
    img{
      border-radius: 50%;
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
      position: relative;
      z-index: 2;
    }
    &--purple{
      border-color: $purple;
      &:before{
        background: rgba(175, 106, 180, 0.4);
      }
    }
  }
  &__info{
    text-align: center;
  }
  &__date{
    margin-top: 20px;
  }
  &__time{
    margin-top: 5px;
  }
}

.team,
.stat{
  margin-top: 60px;
}

.stat{
  &__list{
    margin-top: 30px;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 15px));
    gap: 30px;
  }
}

.summary{
  margin: 60px 0;
  &__subtitle{
    margin: 30px 0;
    text-align: center;
  }
}

.latest-matches{
  margin-top: 60px;
}

.comments{
  margin-top: 60px;
  &__title{
    margin-bottom: 30px;
  }
}

.mvp{
  margin-top: 60px;
  &__items{
    margin-top: 90px;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 20px));
    gap: 90px 30px;
  }
}

.player-stat{
  margin-top: 60px;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__team{
    display: flex;
    align-items: center;
    .result__logo{
      width: 113px;
      height: 113px;
      padding: 10px;
      margin-left: 30px;
    }
  }
  &__table{
    margin-top: 60px;
    overflow-x: auto;
    width: calc(100% + 1.5rem);
    table{
      width: calc(100% - 1.5rem);
      min-width: 1513px;
      border-collapse: separate!important;
      border-spacing: 0 15px!important;
    }
    th{
      line-height: inherit;
    }
    td{
      height: 97px;
      background-color: $black;
      padding: 0 15px;
      width: 115px;
      &:first-child{
        border-radius: 20px 0 0 20px;
        padding-left: 30px;
        width: 100px;
      }
      &:last-child{
        border-radius: 0 20px 20px 0;
        padding-right: 30px;
      }
      &:nth-child(2){
        width: 70px;
      }
      &:nth-child(3){
        width: 320px;
      }
      &:nth-child(4){
        width: 136px;
      }
      &:nth-child(5){
        width: 140px;
      }
      &:nth-child(6),
      &:nth-child(7){
        width: 180px;
      }
    }
  }
}


@media screen and (max-width: 1750px){
  .stat{
    &__list{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 1600px){
  .mvp{
    &__items{
      grid-template-columns: repeat(2,calc(50% - 15px));
    }
  }
}


@media screen and (max-width: 1400px){
  .result{
    padding: 30px 45px;
    &__logo{
      width: 190px;
      height: 190px;
      padding: 18px;
    }
  }
}


@media screen and (max-width: 1200px){
  .result{
    &__logo{
      width: 140px;
      height: 140px;
      padding: 12px;
    }
  }

  .mvp{
    &__items{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 992px){
  .result{
    padding: 20px 30px;
    &__logo{
      width: 100px;
      height: 100px;
      padding: 6px;
    }
  }

  .player-stat{
    .result__logo{
      width: 70px;
      height: 70px;
      margin-left: 15px;
      padding: 8px;
    }
    &__table{
      margin-top: 30px;
    }
  }
}


@media screen and (max-width: 768px){
  .player-stat{
    &__top{
      display: block;
    }
    &__title{
      margin-bottom: 30px;
    }
  }
}


@media screen and (max-width: 576px){
  .mvp{
    &__items{
      gap: 55px;
      margin-top: 55px;
    }
  }

  .stat{
    &__list{
      gap: 15px;
    }
  }
}


@media screen and (max-width: 450px){
  .result{
    border-radius: 10px;
    padding-left: 25px;
    padding-right: 25px;
    &__date{
      font-size: 20px;
      margin-top: 10px;
    }
    &__title{
      font-size: 18px;
    }
    &__logo{
      width: 85px;
      height: 85px;
    }
  }
}
