.match-banner{
  &--comp{
    min-height: 400px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    @media screen and (min-width: 992px){
      padding: 50px 50px 64px;
    }
    .match-banner__img{
      height: 120%;
      width: auto;
      max-width: 100%;
    }
  }
  .result__logo{
    width: 52px;
    height: 52px;
    padding: 2.76px;
    border-color: $green;
    margin-bottom: 30px;
    &:before{
      background: rgba($green, 0.4);
    }
  }
  &__date{
    margin-top: 15px;
  }
  &__buttons{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 15px;
    position: relative;
    z-index: 2;
  }
  &__btn{
    width: 363px;
    height: 45px;
    margin-top: 15px;
    max-width: 100%;
  }
  &__btn2{
    border-color: #fff;
    color: #fff;
    @media screen and (min-width: 1200px){
      width: 298px;
      margin-left: 30px;
    }
  }
}

.info{
  padding: 62px 71px;
  display: grid;
  grid-template-columns: 1fr 1.2fr;
  gap: 30px;
  &__wrap{
    margin-top: 60px;
    display: grid;
    grid-template-columns: 1.25fr 1fr;
    gap: 30px;
  }
  &__desc{
    margin-top: 15px;
  }
  &__teams{
    display: grid;
    grid-template-columns: repeat(5,1fr);
    gap: 30px;
    width: 100%;
    &-wrap{
      padding: 50px;
      display: flex;
      align-items: center;
    }
  }
  &__team{
    &-wrap{
      padding-bottom: 100%;
      position: relative;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
}

.detail-stat{
  margin-top: 60px;
  &__tabs1,
  &__tabs2{
    margin-top: 0;
    li{
      &:not(:last-child){
        margin-right: 60px !important;
      }
    }
  }
  &__tabs1{
    li{
      font-size: 30px !important;
    }
  }
  &__tabs2{
    .m-tabs{
      display: flex;
      justify-content: flex-end;
      list-style: none;
      margin-bottom: 0;
      padding-left: 0;
      li{
        padding-top: 10px;
        position: relative;
        cursor: pointer;
        &:before{
          background: linear-gradient(135deg, #4568dc, #af6ab4);
          border-radius: 8px;
          content: "";
          height: 2px;
          left: 0;
          opacity: 0;
          position: absolute;
          top: 0;
          transition: .2s;
          width: 100%;
        }
      }
      .active{
        &:before{
          opacity: 1;
        }
      }
    }
    li{
      font-size: 20px !important;
    }
  }
  &__content{
    margin-top: 65px;
  }
  &__team-filter{
    display: flex;
    align-items: center;
    .detail-stat__tabs2{
      margin-left: auto;
    }
    .match-filter__select{
      margin-right: 30px;
      margin-left: 0;
    }
  }
  .player-stat__table{
    margin-top: 30px;
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 40px;
  }
  &__btn{
    width: 363px;
    height: 53px;
    max-width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .tab{
    display: none;
  }
}

.m-tab{
  display: none;
}

.league-table{
  overflow-x: auto;
  table{
    width: 100%;
    min-width: 1513px;
    border-collapse: separate!important;
    border-spacing: 0 15px!important;
  }
  th{
    line-height: inherit;
  }
  td{
    height: 97px;
    background-color: $black;
    padding: 0 15px;
    width: 115px;
    &:first-child{
      border-radius: 20px 0 0 20px;
      padding-left: 30px;
      width: 55px;
    }
    &:last-child{
      border-radius: 0 20px 20px 0;
      padding-right: 30px;
    }
    &:nth-child(2){
      width: 285px;
      &~td{
        white-space: nowrap;
        text-align: center;
      }
    }
  }
  &__head{
    &-title{
      text-align: center;
    }
    &-desc{
      text-align: center;
      margin-top: 5px;
    }
  }
  &__team{
    display: flex;
    align-items: center;
  }
  &__icon{
    width: 73px;
    flex-shrink: 0;
    margin-right: 30px;
    img{
      width: 100%;
      display: block;
    }
  }
}

.play-off{
  display: flex;
  align-items: stretch;
  overflow-x: auto;
  width: calc(100% + 1.5rem);
  padding-right: 1.5rem;
  &__col{
    flex-shrink: 0;
    width: 452px;
    padding-top: 100px;
    position: relative;
    @media screen and (min-width: 1200px){
      width: calc(100% / 3 - (160px / 3));
    }
    &:not(:last-child){
      margin-right: 80px;
    }
    &--quart{
      display: grid;
      grid-template-columns: 100%;
      gap: 70px;
    }
    &--semi{
      display: grid;
      grid-template-columns: 100%;
      gap: 250px;
      padding-top: 190px;
      padding-bottom: 90px;
    }
    &--fin{
      padding-top: 370px;
      .play-card{
        margin-bottom: 30px;
      }
    }
  }
  &__top{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    text-align: center;
    text-transform: uppercase;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__stage{
    margin-bottom: 5px;
    position: relative;
    padding-top: 12px;
    &:before{
      background: linear-gradient(135deg, #4568dc, #af6ab4);
      border-radius: 8px;
      content: "";
      height: 2px;
      left: 0;
      position: absolute;
      top: 0;
      transition: .2s;
      width: 100%;
    }
  }
}

.net-quart{
  width: 80px;
  height: 185px;
  border-radius: 20px;
  border: 4px solid #4568dc;
  border-left: none;
  position: absolute;
  left: 100%;
  transform: translateX(-50%);
  &__line{
    width: 80px;
    height: 4px;
    position: absolute;
    top: 50%;
    left: 101%;
    transform: translate(0,-50%);
    background-color: #4568dc;
  }
}

.net-quart1{
  top: 153px;
  border-bottom-right-radius: 0;
  &:before{
    content: '';
    position: absolute;
    width: 4px;
    height: 130%;
    right: -4px;
    top: 50%;
    background-color: #4568dc;
  }
}

.net-quart2{
  bottom: 53px;
  border-top-right-radius: 0;
  &:before{
    content: '';
    position: absolute;
    width: 4px;
    height: 130%;
    right: -4px;
    bottom: 50%;
    background-color: #4568dc;
  }
}

.net-quart3{
  top: 243px;
  height: 364px;
}

.tab-matches{
  @media screen and (min-width: 992px){
    .match-card{
      padding: 15px 30px;
      &:not(:last-child){
        margin-bottom: 15px;
      }
    }
    .played-team{
      &__content{
        padding-top: 0;
      }
      &__players{
        margin-top: 10px;
      }
    }
    .match-result{
      &__score{
        font-size: 50px;
      }
    }
  }
}

.comp-news{
  margin-top: 60px;
  &__title{
    margin-bottom: 30px;
  }
}


@media screen and (max-width: 1900px){
  .league-table{
    width: calc(100% + 1.5rem);
  }
}


@media screen and (max-width: 1500px){
  .info{
    &__wrap{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 1200px){
  .match-banner{
    &__buttons{
      display: block;
    }
  }

  .detail-stat{
    &__tabs1,
    &__tabs2{
      li{
        &:not(:last-child){
          margin-right: 30px !important;
        }
      }
    }
    &__tabs1{
      li{
        font-size: 24px !important;
      }
    }
    &__tabs2{
      li{
        font-size: 18px !important;
      }
    }
    &__team-filter{
      display: block;
    }
  }

  .comp-news{
    .row{
      height: unset !important;
    }
    .row>*{
      position: static !important;
    }
    .card{
      //height: 100%;
      &:first-child{
        width: 100%;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .match-banner--comp{
    min-height: 300px;
  }

  .info{
    &__wrap{
      .m-card{
        padding: 20px 30px;
      }
    }
  }

  .detail-stat{
    &__tabs1,
    &__tabs2{
      overflow-x: auto;
      width: calc(100% + 1.5rem);
      li{
        flex-shrink: 0;
        white-space: nowrap;
      }
    }
    &__tabs1{
      li{
        font-size: 21px !important;
      }
    }
  }
}


@media screen and (max-width: 768px){
  .detail-stat{
    &__tabs1,
    &__tabs2{
      width: calc(100% + 1.5rem * .5);
    }
  }
  .league-table,
  .player-stat__table,
  .play-off{
    width: calc(100% + 1.5rem * .5);
  }
}



@media screen and (max-width: 576px){
  .match-banner{
    &__wrap{
      overflow: hidden;

    }
    &--comp{
      //overflow: hidden;
      .match-banner__img{
        border-radius: 10px;
        height: 100%;
        right: 32%;
        transform: translateX(50%);
        width: unset;
        max-width: unset;
        object-fit: unset;
      }
    }
  }

  .info{
    gap: 20px;
    &__teams{
      gap: 20px;
    }
  }

  .play-off{
    &__col{
      width: 320px;
    }
  }
}
