.team-info__statistic {
  .statistic__item {
    background: $black !important;
    &:hover{
      background: linear-gradient(114.78deg, #4568dc -79.31%, #af6ab4 380.73%), #1e1e20!important;
    }
  }
}

.team-player-cards{
  &__litera{
    position: absolute;
    top: -20px;
    right: -40px;
    width: 41px;
    z-index: 5;
    img{
      width: 100%;
      display: block;
    }
  }
  &__item-link{
    display: block;
    position: relative;
  }
  &__bottom{
    position: absolute;
    bottom: -40px;
    width: 139px;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 5;
  }
  &__position{
    width: 71px;
    display: block;
  }
  &__nickname{
    display: flex;
    align-items: center;
    svg{
      margin-right: 5px;
    }
  }
}

.teams-filter{
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}


@media screen and (max-width: 1400px){
  .teams-filter{
    margin-bottom: 60px;
  }
}

@media screen and (max-width: 1200px){
  .teams-filter{
    .match-filter__select{
      width: 100% !important;
    }
  }
}
