.best-matches__item-stars-little{
  max-height: 46%;
  display: block;
}

.best-matches__item-stars{
  height: 164px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  img{
    width: auto;
  }
}

.best-matches__item-stars-big{
  height: 100%;
}

.best-matches__item-stars-middle{
  height: 85%;
}

.career__player-position{
  width: 77px;
}

.career__player-positions{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  width: 210px;
  margin: 0 auto;
  img{
    width: 65px;
    margin: 5px 5px 0 0;
  }
}

#content{
  background-image: url(../images/site-bg.png);
  position: relative;
  background-position: center bottom -2%;
  @media screen and (min-width: 1200px){
    padding-bottom: 600px;
  }
  &:before{
    content: '';
    position: absolute;
    max-width: 100%;
    width: 1141px;
    height: 1069px;
    background-image: url(../images/light.png);
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: 100% auto;
    bottom: 50px;
    left: 50%;
    transform: translateX(-50%);
    z-index: -1;
  }
}

@media screen and (max-width: 992px){
  .best-matches__item-stars{
    height: 120px;
  }
}
